
@import "kernel";

@mixin selection {
    &::-moz-selection { @content }
    &::selection { @content }
}

@mixin scrollbars {
    &::-webkit-scrollbar {
        width: 6px;
    }

    &::-webkit-scrollbar-track {
        background: #232733;
        box-shadow: inset 0 1px 1px rgba(#000, .5);
        border-radius: 3px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 3px;
        background: #464d5e;
        box-shadow: inset 0 0 0 1px #232733;
    }
}

.page-try-online {
    width: 100vw;
    display: flex;
    height: 100vh;
    background: #3d4456;
    box-sizing: border-box;
    padding-top: 48px;

    .layout {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        align-items: stretch;
        flex-direction: column;
        color: $color-dark-white;

        $height: 36px;

        .header {
            height: $height;
            padding: 0 10px;
            width: 100%;
            background: #232733;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            box-shadow: inset 0 -1px 0 rgba(#000, .1);

            .tab {
                cursor: default;
                top: 6px;
                height: 32px;
                position: relative;
                box-shadow: inset 0 1px 0 rgba(#fff, .1);
                background: #3d4456;
                background: linear-gradient(to bottom, lighten(#3d4456, 5%) 0, #3d4456 100%);
                display: flex;
                font-size: 12px;
                justify-content: center;
                align-items: center;
                padding: 0 20px;
                margin-right: 8px;
                color: $color-light-silver;
                border-radius: 2px 2px 0 0;
            }

            & /deep/ .button {
                position: relative;
                top: 1px;
                a, button {
                    box-shadow:
                        0 0 1px rgba(#000, .1),
                        inset 0 0 1px rgba(#fff, .1);
                    height: 22px;
                    line-height: 22px;
                    padding: 0 10px;
                }
            }

            .version {
                margin: 0 30px 0 10px;
                font-size: 11px;
                line-height: 11px;
                color: $color-silver;
                justify-self: flex-end;
            }
        }

        .editor {
            width: 100%;
            height: 95%;
            height: calc(100% - #{$height});
            background: #3d4456;
            box-shadow: inset 0 1px 0 rgba(#fff, .05);
            flex-shrink: 1;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            padding: 20px;

            .editor-result {
                height: 100%;
                overflow-y: scroll;
                width: 50%;
                position: relative;
                left: 10px;
                border-radius: 3px;
                font-family: $font-mono;
                font-size: 12px;
                @include scrollbars;

                .error {
                    border-radius: 3px 3px 0 0;
                    color: rgba($color-white, .8);
                    background: rgba($color-red, .9);
                    padding: 15px 25px;
                    box-shadow: 0 1px 0 rgba(#000, .1);
                    white-space: pre;
                    word-break: keep-all;
                }

                .trace {
                    .trace-title {
                        padding: 5px 15px;
                        text-transform: uppercase;
                        background: rgba(#fff, .1);
                        margin: 15px 0 5px 0;
                    }

                    .trace-item {
                        display: block;
                        box-sizing: border-box;
                        padding: 0 15px;
                        width: 100%;
                        font-size: 12px;
                        line-height: 18px;
                        color: #c5c9d3;
                        white-space: pre;
                        word-break: keep-all;

                        &:nth-child(2n + 1) {
                            background: rgba(#fff, .02);
                        }
                    }
                }
            }

            .editor-code {
                box-sizing: border-box;
                padding-left: 20px;
                height: 100%;
                width: 50%;
                position: relative;
                overflow: hidden;
            }

            .code {
                white-space: pre;
                word-break: keep-all;
                border: none;
                background: none;
                resize: none;
                width: 100%;
                min-height: 100%;
                position: absolute;
                color: $color-gray;
                line-height: 20px;
                overflow: visible;
                font-family: $font-mono;
                font-size: 14px;
                z-index: 8;
                outline: none;
                tab-size: 4;
                hyphens: none;
                caret-color: $color-gray;
                left: 0;
                top: 0;
                margin: 0;
                padding: 0;

                @include selection {
                    background: #6a637a;
                }

                &.highlight {
                    z-index: 9;
                    pointer-events: none;
                    overflow: visible !important;
                }

                &.text {
                    color: #3d4456;
                    overflow-y: scroll;
                    scroll-behavior: smooth;
                    @include scrollbars;
                }
            }
        }
    }
}



