
@import "kernel";

.page-donate {
    box-sizing: border-box;
    padding: 40px 50px;
    box-shadow: 0 0 0 1px $color-border;
    border-radius: 3px;
    max-width: 500px;
    margin: 198px auto 150px auto;
    position: relative;
    display: block;

    /deep/ .input {
        width: 400px;
    }

    .form-element-description,
    .form-element {
        box-sizing: border-box;
        padding: 30px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        img {
            width: 100px;
            height: auto;
            justify-self: flex-end;
        }

        &.ya-logo {
            justify-content: center;
        }
    }

    .form-element-description {
        border-radius: 3px;
        padding: 20px;
        margin: 20px 0;
        font-size: 12px;
        background: rgba($color-bg, .7);
        box-shadow: 0 0 0 1px $color-border;
    }
}



